      margin: 0 auto;
      max-height: 600px;
      font-size: 1.8rem;
      line-height: 2.4rem;

      .align-left {
        @media (max-width: 768px) {
          float: none;
        }
      }

      img {
        border-radius: 50%;
        border: 8px solid #eee;
        margin: 0 3rem 1.5rem 0;
        width: 216px;
        height: 216px;
        @media (max-width: 768px) {
          float: none;
          display: block;
          //width: 50%;
          //height: auto;
