      max-height: 600px;
      padding-right: 1.5rem;
    }

    .read-more a {
      color: #293749;
    }
  }

  /** Feedback **/
  .section.feedback {
    background: url(../images/client-testimonials-background.jpg) bottom center;
    background-size: cover;
    background-color: #fff;
    color: #575756;

    h2 {
      color: #6686A2;
    }

    .line {
      background-color: #81a5bf;
    }

    h3.title {
      font-size: 2rem;
      color: #293749;
      margin: 0 0 1.5rem 0;
    }

    .about-person {
      margin-bottom: 1.5rem;
      color: #6686A2;

      span {
        color: #6686A2;

        &:not(:last-child) {
          &:after {
            content: "|";
          }
        }
      }
    }

    .name {
    }

    .position {
    }

    .country {
    }

    .field--name-field-image-media {
      img {
        width: 150px;
        height: 150px;
        margin: 0 auto 1.5rem auto;
        border-radius: 50%;
        border: .8rem solid #eee;
        opacity: .8;

        &:hover {
          opacity: 1;
        }
      }
    }

    .project-info {
      margin-bottom: 1.5rem;
    }

    .field--name-field-project-title {
      color: #293749;

      a {
      }

      .field__item {
        &:not(:last-child) {
          &:after {
            content: ",";
          }
        }
      }
    }

    blockquote {
      border-left: .1rem solid #bbb;
      font-style: italic;
      padding: 0.5em 1rem;
      text-align: justify;
      margin: 0;
      font-size: inherit;
      color: #575756;

      &:before, &:after {
        font-family: helvetica, arial, sans-serif;
        color: #6686A2;
        font-size: 3em;
        line-height: 0.1em;
      }

      &:before {
        content: "\201c";
        vertical-align: -.4em;
        margin-left: 0;
        margin-right: 0.2em;
      }

      &:after {
        content: "\201d";
        vertical-align: -.45em;
        margin-left: 0.2em;
      }

      > p:first-child {
        display: inline;
      }
    }

    .read-more a {
      color: #293749;
    }
  }

